import Vue from "vue";
import VueRouter from "vue-router";
import viewsUser from "../views/user/index.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "index",
    component: viewsUser,
    meta: { 
      title:"首页"
    }
  }

];

const router = new VueRouter({
  routes
});

export default router;
